import { css } from "lit";
export const listStyle = css `

  ul {
    list-style-type:          none;
    padding:                  0;
    margin:                   0;
  }

  li {
    display:                  grid;
  }

  .divider {
    border-top:     2px dotted black;
    border-bottom:  2px dotted black;
    text-align:     center;
    width:          80%;
    margin:         auto;
  }

`;
